// ---------------- 01. General-use classes & Accessibility

.assistive-text,
.visuallyhidden,
.screen-reader-text {
	position: absolute !important;
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0 !important;
	border: 0 !important;
	height: 1px !important;
	width: 1px !important;
	overflow: hidden;
}

.cover-button {
	display: flex;
	flex-direction: column;
	.wp-block-cover__inner-container {
		flex: 1;
		display: flex;
		flex-direction: column;

		h2 {
			flex: 1;
			height: 100%;
			width: 100%;
			margin: 0;
			display: flex;

			a {
				flex: 1;
				display: flex;
				align-items: center;
				justify-content: center;
				border:1px solid transparent;

				&:hover,
				&:focus,
				&:target {
					background: rgba( 0, 0, 0, 0.4 );
					border: 1px solid #fff;
				}
			}
		}
	}
}

// ---------------- 02. Mixins
// Style mixins

// Responsive mixins
// using breakpoint, but helping readability

@mixin phone-only {
	@include breakpoint($phone) {
		@content;
	}
}

@mixin tablet-only {
	@include breakpoint($tablet) {
		@content;
	}
}

@mixin tablet-portrait {
	@include breakpoint($tablet-portrait) {
		@content;
	}
}

@mixin tablet-landscape {
	@include breakpoint($tablet-landscape) {
		@content;
	}
}

@mixin desktop-only {
	@include breakpoint($desktop) {
		@content;
	}
}

@mixin large-desktop {
	@include breakpoint($large-desktop) {
		@content;
	}
}

@mixin tablet-down {
	@include breakpoint($tablet-portrait-down) {
		@content;
	}
}

@mixin tablet-up {
	@include breakpoint($tablet-landscape-up) {
		@content;
	}
}

@mixin phone-landscape {
	@include breakpoint($phone-landscape) {
		@content;
	}
}


// Gradients
// from https://webgradients.com

@mixin plum-gradient {
	background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

@mixin grey-purple-gradient {
	background-image: linear-gradient(to top, #505285 0%, #585e92 12%, #65689f 25%, #7474b0 37%, #7e7ebb 50%, #8389c7 62%, #9795d4 75%, #a2a1dc 87%, #b5aee4 100%);
}

@mixin dark-aqua-gradient {
	background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);
}

@mixin dark-blue-gradient {
	background-image: linear-gradient(to top, #09203f 0%, #537895 100%);
}

@mixin medium-blue-gradient {
	background-image: linear-gradient(to top, #00c6fb 0%, #005bea 100%);
}

@mixin light-blue-gradient {
	background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}

@mixin green-gradient {
	background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

@mixin orange-gradient {
	background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
}

@mixin sunset-gradient {
	background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
}

@mixin light-grey-gradient {
	background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

// ---------------- 03. Functions

@function hexToRGB($hex) {
	@return red($hex), green($hex), blue($hex);
}

